.container {
  min-width: 360px;
  margin-left: auto;
  margin-right: auto;
}

.wrapper {
  height: 32px;
  left: 0px;
  top: 0px;
  background: var(--color-blue);
}
.header__text {
  font-family: 'Basis Grotesque Pro';
  font-weight: 400;
  font-size: 10px;
  line-height: 32px;
  margin: 0;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--color-white);
}
